.dialog-pane {
    &-background {
        background-color: gray;
        opacity: 0.5;
        width: 100%;
        height: 100%;
        position: relative;
    }

    &-dialog {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 10px;
        vertical-align: middle;
        text-align: center;
        color: white;
    }
}



